body {
    font-family: 'Kanit';
    color:black
  }
  
  #toast-container {
    font-family:'Kanit';
    color:black !important;
    margin-top: 1rem;
  }
  
  #toast-container.toast-top-full-width>.toast {
    font-family:'Kanit';
    color:black !important;
    max-width: 570px;
  }

  button.toast-close-button {
    color:black;
}
  
  #toast-container>.toast-info {
    background-color: rgba(247, 220, 111,1);
    font-family:'Kanit';
    color:black !important;
    font-size: medium;
    opacity:1;
    box-shadow:0px 0px 10px rgba(0,0,0,0.3);
    background-size: 0 !important;
  }
  
  #toast-container>.toast-info:hover {
    background-color: rgba(244, 208, 63,1);
    font-family:'Kanit';
    color:black !important;
    opacity:1;
    box-shadow:0px 0px 10px rgba(0,0,0,0.6);
  }
  
  